import {
	mapState
} from 'vuex';
import {
	myrequest,
	collectionlisturl,
	myget
} from '../../api.js';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			uid: "",
			cartList: [],
		};
	},
	onLoad() {
		this.loadData();
	},
	onShow() {
		this.loadData();
	},
	methods: {
		//请求数据
		async loadData() {
			this.uid = await myget("uid");
			let list = await myrequest(collectionlisturl, 'POST', {
				uid: this.uid
			});
			this.cartList = list["data"];

			function dateFormat(fmt, date) { //author: meizz 
				let o = {
					"M+": date.getMonth() + 1, //月份 
					"d+": date.getDate(), //日 
					"h+": date.getHours(), //小时 
					"m+": date.getMinutes(), //分 
					"s+": date.getSeconds(), //秒 
					"q+": Math.floor((date.getMonth() + 3) / 3), //季度 
					"S": date.getMilliseconds() //毫秒 
				};
				if (/(y+)/.test(fmt))
					fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
				for (var k in o)
					if (new RegExp("(" + k + ")").test(fmt))
						fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
				return fmt;
			};
			this.cartList.forEach(function(item) {
				item.time = dateFormat("yyyy-MM-dd hh:mm:ss", new Date(item.time));
			});
			console.log(this.cartList);
		},
		navTo(item){
			uni.navigateTo({
				url: `/pages/service/product?id=${item.sid.id}`
			})				
		}
	}
}
